.load_content{
      width: 100%;
      height: 100vh;
      display: grid;
      place-content: center;
      justify-items: center;
      gap: 1rem;
      background: var(--bg-200);
      position: fixed;
      top: 0;
      left: 0;
      z-index: 999;
}

.load_circle{
      width: 10rem;
      height: 10rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem rgba(0,0,0,.3);
      position: relative;
}

.load_circle:before{
      width: 10rem;
      height: 10rem;
      border: solid .2rem var(--primary);
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(0);
      border-radius: 50%;
      border-left: solid .2rem transparent;
      border-right: solid .2rem transparent;
      border-bottom: solid .2rem transparent;
      animation: spin_load 1s linear infinite;
}

@keyframes spin_load{
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

.load_content span{
      margin-top: 3rem;
      text-transform: uppercase;
      color: var(--primary);
      font-size: 1.4rem;
}