/**
* MODAL
*/

.modal_content{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10;
      background: rgba(0,0,0,.8);
      align-items: center;
      justify-content: center;
      visibility: hidden;
      opacity: 0;
      transition: .3s ease-in-out;
}

.modal_content.show{
      visibility: visible;
      opacity: 1;
}

.modal_content__img{
      width: fit-content;
      height: fit-content;
      max-width: 80vh;
      transform: scale(0);
}

.modal_content.show .modal_content__img{
      animation: show_img .3s .3s forwards;
}

@keyframes show_img {
      0%{
            transform: scale(0);
      }
      80%{
            transform: scale(1.4);
      }
      100%{
            transform: scale(1);
      }
}





.btn_close{
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      background: var(--primary);
      border-radius: 50%;
      right: 5rem;
      bottom: 5rem;
      position: absolute;
}

.btn_close *{
      color: var(--light);
}