/**
* BANNER 
*/

.banner_content{
      width: 100%;
      height: 35rem;
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      position: relative;
}

.banner_content__img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, rgba(32,33,36, .8) 60%, var(--bg-200) 100%);
}