/**
* ONLINE
*/

.online_content{
      position: absolute;
      width: fit-content;
      height: 2.6rem;
      background: rgba(0,0,0,.5);
      padding: 0 .6rem;
      align-items: center;
      gap: .5rem;
      border-radius: 999px;
      top: 10rem;
      left: 0;
      transform: translateX(-6rem);
}

.online_content .dots{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.online_content .dots.green{
      background: var(--green);
}

.online_content .dots.red{
      background: var(--red);
}

.online_content span{
      font-size: 1rem;
      padding-right: 1rem;
      white-space: nowrap;
}