/**
* MAIN CONTENT
*/

.main_page{
      margin-top: 8rem;
      gap: 4rem;
      justify-content: space-between;
}

.section_content{
      flex-grow: 1;
}

.about_content{
      width: 100%;
      height: auto;
      gap: 1rem;
      padding-bottom: 2rem;
      position: relative;
}

.about_content:before{
      width: 100%;
      height: .2rem;
      background: repeating-linear-gradient(90deg, var(--primary), var(--light), var(--primary) 50%);
      background-size: 200%;
      background-position: right top;
      animation: move_bg 1s linear infinite; 
      left: 0;
      bottom: 0;
      border-radius: 999px;
}

@keyframes move_bg{
      100%{
            background-position: left top;
      }
}

.about_icon *{
      font-size: 4rem;
      color: var(--border);
}

.about_content_text h1{
      font-size: 4.5rem;
      color: var(--primary);
}

.about_description{
      margin-top: 1rem;
}

.about_description p{
      color: var(--text-200);
}

.gallery_content{
      margin-top: 1rem;
}

.gallery_content__grid{
      width: 100%;
      height: auto;
      columns: 2;
      column-gap: .5rem;
      break-inside: avoid;
}

.card_gallery{
      position: relative;
      width: 100%;
      height: fit-content;
}

.card_gallery__img{
      width: 100%;
      height: fit-content;
      position: relative;
      background: var(--bg-100);
      border-radius: 1rem;
      overflow: hidden;
}

.card_gallery__img:before{
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.4);
      top: 0;
      left: 0;
      opacity: 0;
      transition: .3s ease-in-out;
}

.card_gallery:hover .card_gallery__img:before{
      opacity: 1;
} 

.card_gallery__img img{
      border-radius: 1rem;
}

.btn_zoom{
      width: 5rem;
      height: 5rem;
      position: absolute;
      top: calc(50% - 2.5rem);
      left: calc(50% - 2.5rem);
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.3);
      border-radius: 50%;
      transform: scale(0);
      transition: .2s ease-in-out;
}

.card_gallery:hover .btn_zoom{
      transform: scale(1);
}

.btn_zoom *{
      color: var(--light);
      font-size: 2rem;
}


/**
* ASIDE
*/

.aside_content{
      width: 32.8rem;
      height: auto;
      flex-shrink: 0;
}

.friends_content{
      width: 100%;
      height: auto;
      margin-bottom: 2rem;
      background: var(--bg-100);
      padding: 2rem;
      border-radius: .6rem;
      border: solid .1rem var(--border);
}

.friends_content h2{
      font-size: 2.5rem;
      line-height: 3rem;
      color: var(--primary);
      font-weight: 600;
      display: flex;
      gap: 1rem;
      align-items: center;
}

.friends_content h2 small{
      width: 2.6rem;
      height: 2.6rem;
      display: flex;
      align-items: center;
      justify-contenT: center;
      background: var(--bg-200);
      border-radius: 50%;
      color: var(--light);
      font-size: 1rem;
      border: solid .1rem var(--border);
}

.friends_content__group{
      margin-top: 2rem;
      border-top: solid .1rem var(--border);
      padding-top: 2rem;
}

.friend_row{
      width: 100%;
      height: 5rem;
      margin-bottom: 2rem;
      gap: 1rem;
}
.friend_row__avatar{
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      overflow: hidden;
}

.friend_row__data{
      flex-direction: column;
}

.friend_row__data strong{
      font-size: 1.4rem;
      font-weight: 500;
}

.friend_row__data p{
      color: var(--text-200);
      font-size: 1rem;
}

.friend_row__data a{
      display: flex;
      align-items: center;
      color: var(--light);
      text-transform: uppercase;
      font-size: .8rem;
      background: var(--primary);
      padding: .3rem .75rem;
      gap: .5rem;
      border-radius: .2rem;
}

.friend_row__data *{
      font-size: 1.2rem;
}

.video_row{
      width: 32.8rem;
      heighT: 18.5rem;
      position: relative;
      overflow: hidden;
      border-radius: 1rem;
      margin-bottom: 2rem;
}

.video_row iframe{
      border: none;
      outline: none;
}