/**
* HEADER 
*/

.header_content{
      padding-top: 9.5rem;
      align-items: center;
      justify-content: space-between;
}

.header_user{
      gap: 1rem;
      align-items: center;
}

.header_user__avatar{
      width: 17rem;
      height: 17rem;
      border: solid .2rem var(--primary);
      border-radius: 50%;
      position: relative;
}

.header_user__avatar:before{
      width: 16rem;
      height: 16rem;
      background: conic-gradient(var(--light), var(--primary), var(--light), var(--primary), var(--light));
      border-radius: 50%;
      top: calc(50% - 8rem);
      left: calc(50% - 8rem);
      animation: spin 2s ease-in-out infinite;
}

@keyframes spin {
      100%{
            transform: rotate(360deg);
      }
}

.user_avatar__img{
      width: 15rem;
      height: 15rem;
      background: var(--bg-200);
      position: relative;
      border-radius: 50%;
      margin-left: calc(50% - 7.5rem);
      margin-top: calc(50% - 7.5rem);
      box-shadow: 0 0 0 .3rem var(--bg-200);
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.user_avatar__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.header_user__data{
      position: relative;
}

.header_user__data h1{
      font-size: 5rem;
      color: var(--primary);
      text-shadow: .5rem .5rem 5px rgba(0,0,0,.4);
      line-height: 4.5rem;
}

.header_user__data p{
      text-transform: capitalize;
      font-size: 1.8rem;
}

.header_user__data > span{
      font-weight: 500;
      color: var(--text-200);
      font-size: 2rem;
}

.header_location{
      align-items: center;
      gap: 1rem;
}

.location_icon{
      position: relative;
      animation: pulse 1s ease-in-out infinite;
}

.location_icon *{
      font-size: 3rem;
      color: var(--primary);
}

@keyframes pulse{
      0%{
            transform: translateY(0);
      }
      50%{
            transform: translateY(-1rem);
      }
      100%{
            transform: translateY(0);
      }
}

.header_location p{
      text-transform: capitalize;
      font-size: 2rem;
      font-weight: 600;
}